// validation.js
/**
 * –í–∞–ª–∏–¥–∞—Ü–∏—è Card Holder
 * –ü—Ä–æ–ø—É—Å–∫–∞–µ—Ç —Å—Ç—Ä–æ–∫—É —Å –¥–≤—É–º—è —Å–ª–æ–≤–∞–º–∏, —Ä–∞–∑–¥–µ–ª–µ–Ω–Ω—ã–º–∏ –ø—Ä–æ–±–µ–ª–∞–º–∏,
 * —Å–æ—Å—Ç–æ—è—â—É—é —Ç–æ–ª—å–∫–æ –∏–∑ –ª–∞—Ç–∏–Ω—Å–∫–∏—Ö –±—É–∫–≤
 */
export function validateCardHolder(value) {
  if (!value || typeof value !== 'string') {
    return false;
  }

  const trimmedValue = value.trim();

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ —Ü–∏—Ñ—Ä
  if (/\d/.test(trimmedValue)) {
    return false;
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –∫–∏—Ä–∏–ª–ª–∏—Ü—É
  if (/[–∞-—è–ê-–Ø—ë–Å]/.test(trimmedValue)) {
    return false;
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –¥–≤–∞ —Å–ª–æ–≤–∞, —Ä–∞–∑–¥–µ–ª–µ–Ω–Ω—ã—Ö –ø—Ä–æ–±–µ–ª–æ–º
  const words = trimmedValue.split(/\s+/);

  if (words.length !== 2) {
    return false;
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞, —á—Ç–æ –æ–±–∞ —Å–ª–æ–≤–∞ —Å–æ—Å—Ç–æ—è—Ç —Ç–æ–ª—å–∫–æ –∏–∑ –ª–∞—Ç–∏–Ω—Å–∫–∏—Ö –±—É–∫–≤
  const latinLettersOnly = /^[a-zA-Z]+$/;

  return words.every((word) => latinLettersOnly.test(word));
}

/**
 * –í–∞–ª–∏–¥–∞—Ü–∏—è Card Number
 * –ü—Ä–æ–ø—É—Å–∫–∞–µ—Ç —Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫—É –∏–∑ —Ü–∏—Ñ—Ä (—Å –ø—Ä–æ–±–µ–ª–∞–º–∏ –∏–ª–∏ –±–µ–∑)
 * –î–ª–∏–Ω–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –æ—Ç 13 –¥–æ 16 —Ü–∏—Ñ—Ä
 */
export function validateCardNumber(value) {
  if (!value || typeof value !== 'string') {
    return false;
  }

  // –£–±–∏—Ä–∞–µ–º –ø—Ä–æ–±–µ–ª—ã –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏
  const digitsOnly = value.replace(/\s/g, '');

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –Ω–µ—Ü–∏—Ñ—Ä–æ–≤—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤
  if (!/^\d+$/.test(digitsOnly)) {
    return false;
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–ª–∏–Ω—ã (—Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–µ –∫–∞—Ä—Ç—ã: 13-16 —Ü–∏—Ñ—Ä)
  const length = digitsOnly.length;

  return length >= 13 && length <= 16;
}

/**
 * –í–∞–ª–∏–¥–∞—Ü–∏—è CVV/CVC
 * –ü—Ä–æ–ø—É—Å–∫–∞–µ—Ç —Å—Ç—Ä–æ–∫—É —Ä–æ–≤–Ω–æ –∏–∑ 3 —Ü–∏—Ñ—Ä–æ–≤—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤
 */
export function validateCVV(value) {
  if (!value || typeof value !== 'string') {
    return false;
  }

  const trimmedValue = value.trim();

  // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ —Ç–æ—á–Ω—É—é –¥–ª–∏–Ω—É 3 —Å–∏–º–≤–æ–ª–∞
  if (trimmedValue.length !== 3) {
    return false;
  }

  // –ü—Ä–æ–≤–µ—Ä–∫–∞, —á—Ç–æ –≤—Å–µ —Å–∏–º–≤–æ–ª—ã - —Ü–∏—Ñ—Ä—ã
  return /^\d{3}$/.test(trimmedValue);
}

/**
 * –í–∞–ª–∏–¥–∞—Ü–∏—è –¥–∞—Ç—ã –∫–∞—Ä—Ç—ã (MM/YY)
 */
export function validateCardDate(value) {
  if (!value || typeof value !== 'string') {
    return false;
  }

  // –§–æ—Ä–º–∞—Ç MM/YY
  const datePattern = /^(0[1-9]|1[0-2])\/\d{2}$/;

  return datePattern.test(value);
}

/**
 * –û–±—â–∞—è –≤–∞–ª–∏–¥–∞—Ü–∏—è –≤—Å–µ—Ö –ø–æ–ª–µ–π —Ñ–æ—Ä–º—ã
 */
export function validateForm(cardHolder, cardNumber, cardDate, cvv) {
  return {
    cardHolder: validateCardHolder(cardHolder),
    cardNumber: validateCardNumber(cardNumber),
    cardDate: validateCardDate(cardDate),
    cvv: validateCVV(cvv),
    isValid: function () {
      return this.cardHolder && this.cardNumber && this.cardDate && this.cvv;
    },
  };
}
